import swal from 'sweetalert';

export const insert_DeliveryDetails = (deliveryDetails, setdeliveryDetails) => {

  deliveryDetails.phoneNo = '+966' + deliveryDetails.phoneNo;

  let url = "http://zinfogapi.herokuapp.com/about/	";

  fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(deliveryDetails)
  }).then((result) => {
    result.json().then((resp) => {
      console.warn("resp", resp)
      swal({
        title: "Success",
        text: "Your Request is Approved.",
        icon: "success",
        button: "ok",
      });
      setdeliveryDetails({
        source: '',
        pickUpFrom: '',
        deliveryTo: '',
        yourName: '',
        phoneNo: '',
        emailId: '',
        truckType: '',
      });
    })
  })
}





