import React, { useState } from 'react';
import '../App.css';
import { insert_DeliveryDetails } from './utils';
import { IoMdPin } from "react-icons/io";
import { Button, Modal } from 'react-bootstrap';


const MainContent = () => {

  const [deliveryDetails, setdeliveryDetails] = useState({
    source: '',
    pickUpFrom: '',
    deliveryTo: '',
    yourName: '',
    phoneNo: '',
    emailId: '',
    truckType: '',
  });

  const [show, setShow] = useState(false);

  const handleShow = () => setShow(true);
  const handleClose = () => setShow(false);

  return (
    <section>
      <section className="thirdsection">
        <div className="container mt-3 thirdcontainer">
          <h4>From Where You Get About Trucker</h4>
          <div className="gty">
            <div className="input-group mb-3  " id="abb">
              <select style={{ borderRadius: "45px 45px 45px 45px", fontWeight: "bold" }}
                onChange={(e) => { setdeliveryDetails({ ...deliveryDetails, source: e.target.value }) }} value={deliveryDetails.source} >
                <option value="" disabled selected>Select Source</option>
                <option value="users" >Users</option>
                <option value="advertisements">Advertisements</option>
                <option value="socialmedias">Social Medias</option>
              </select>
            </div>
          </div>
          <h4>Fill The Delivery Details</h4>
          <div className="fgtu">
            <div className="input-group mb-3">
              <input type="text" className="form-control thirdsectiontext1" placeholder="Pickup From" style={{ borderRadius: "45px 0px 0px 45px" }}
                onChange={(e) => { setdeliveryDetails({ ...deliveryDetails, pickUpFrom: e.target.value }) }} value={deliveryDetails.pickUpFrom} />
              <div className="input-group-append">
                <span className="input-group-text  gghh" style={{ borderRadius: "0px 45px 45px 0px" }}><a href=""><div className="lloop"><IoMdPin /></div></a></span>
              </div>
            </div>
          </div>
          <div className="fgtu">
            <div className="input-group mb-3">
              <input type="text" className="form-control" placeholder="Delivery To" style={{ borderRadius: "45px 0px 0px 45px" }}
                onChange={(e) => { setdeliveryDetails({ ...deliveryDetails, deliveryTo: e.target.value }) }} value={deliveryDetails.deliveryTo} />
              <div className="input-group-append">
                <span className="input-group-text gghh" style={{ borderRadius: "0px 45px 45px 0px" }}><a href=""><div className="lloop"><IoMdPin /></div></a></span>
              </div>
            </div>
          </div >
          <div className="fgtu">
            <div className="input-group mb-3">
              <input type="text" className="form-control" placeholder="Your Name" style={{ borderRadius: "45px 45px 45px 45px" }}
                onChange={(e) => { setdeliveryDetails({ ...deliveryDetails, yourName: e.target.value }) }} value={deliveryDetails.yourName} />
            </div>
          </div>
          <div className="fgtu">
            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span className="input-group-text" style={{ borderRadius: "45px 0px 0px 45px" }}>+966</span>
              </div>
              <input type="text" className="form-control" placeholder="Your Phone No" style={{ borderRadius: "0px 45px 45px 0px" }}
                onChange={(e) => { setdeliveryDetails({ ...deliveryDetails, phoneNo: e.target.value }) }} value={deliveryDetails.phoneNo} />
            </div>
          </div>
          <div className="fgtu">
            <div className="input-group mb-3">
              <input type="text" className="form-control" placeholder="Your Email Id" style={{ borderRadius: "45px 45px 45px 45px" }}
                onChange={(e) => { setdeliveryDetails({ ...deliveryDetails, emailId: e.target.value }) }} value={deliveryDetails.emailId} />
            </div>
          </div>
          <div className="fgtu">
            <div className="input-group mb-3  " id="abb">
              <select className="form-control1" style={{ borderRadius: "45px 45px 45px 45px", fontWeight: "bold" }}
                onChange={(e) => { setdeliveryDetails({ ...deliveryDetails, truckType: e.target.value }) }} value={deliveryDetails.truckType}>
                <option value="" disabled selected>Select Truck Type</option>
                <option value="Semi-Trailer Truck">Semi-Trailer Truck</option>
                <option value="Extra Duty Truck">Extra Duty Truck</option>
                <option value="18-Wheeler Truck">18-Wheeler Truck</option>
                <option value="Tanker Truck">Tanker Truck</option>
              </select>
            </div>
          </div>
          <div className="distclass">Distance    00.0  km</div>
          <div className="thirdanchor">
            <div className="thirdanchors" onClick={() => { insert_DeliveryDetails(deliveryDetails, setdeliveryDetails) }}   > <a href="#" >Submit</a></div>
          </div>
        </div>
        <div className="seemore24"><a href="#" className="seemore" onClick={handleShow}>Learn More</a></div>



      </section>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton style={{fontSize:"18px",fontWeight:"bold",textAlign:"center"}}> Dear Customer</Modal.Header>

        <Modal.Body style={{fontSize:"18px",}}> Fill All The Text Fields And Submit ,You Will Get An Approved Message After Admins Verification</Modal.Body>

        <Modal.Footer style={{textAlign:"center"}}> <Button onClick={handleClose} style={{backgroundColor:"#2e0000",border:"none"}}>Thank You</Button></Modal.Footer>


      </Modal>
    </section>
  )
}

export default MainContent;